@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.activity-panel{margin-top:0;display:flex;flex-direction:column}.activity-panel__activity{display:flex}.activity-panel__activity th,.activity-panel__activity td{width:25%;display:flex;justify-content:center;background-color:#343536;padding:1rem 0;border:2px solid #1f2020;border-radius:5px}.activity-panel__activity th{text-transform:uppercase;font-weight:600;background-color:#19191a;border:none}

.clock-panel{justify-content:space-between}.clock-panel__input{width:70%;font-size:1rem;border:none;margin-right:1rem;padding:1rem;border-radius:5px;display:flex;align-self:flex-end}

html {
  font-size: 16px;
}

body {
  background-color: #030303;
  font-family: 'Open Sans', sans-serif;
  color: #D7DADC;
}

h1 {
  margin: 0;
}

.App {
  display: flex;
  font-size: 16px;
  flex-direction: column;
  align-items: center;
}

.box {
  box-shadow: 0 0 0 0 rgba(34, 36, 38, .15) inset;
  border: 1px solid #343536;
  background-color: #1A1A1B;
  width: 50%;
  height: auto;
  margin: 1rem;
  padding: 1rem;
  border-radius: 5px;
  display: flex;
}

.button {
  font-size: 1rem;
  width: 30%;
  background-color: #f1f1f1;
  color: #fff;
  font-weight: 800;
  padding: 1rem;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  text-align: center;
  box-shadow: 0 0 0 0 rgba(34, 36, 38, .15);
}

.button:disabled {
  cursor: default;
  opacity: .45!important;
  background-image: none!important;
  box-shadow: none!important;
  pointer-events: none!important;
}

.button:hover {
  color: white;
}

.primary {
  background-color: #2185d0;
}

.danger {
  background-color: #db2828;
}

@media only screen and (max-width: 600px) {
  .mobile {
    width: 80%;
    font-size: .8rem;
  }
  .clock-panel__input {
    height: 100%;
  }
}
